@use "../base/colors" as *;
@use "../abstracts/mixins" as *;

.footer{
    text-align: center;
    background-color: #000;
    line-height: 80px;
    border-top: 2px solid #1a1a1a;
    &__text{
        font-size: clamp(1.6rem, 1.3333rem + 0.7407vw, 2rem);
        color: #C7C7C7;
    }
    &__logo{
        color: #fff;
        font-weight: 500;
    }
}