@use "./base/colors" as *;
@use "./base/reset" as *;
@use "./layout/header" as *;
@use "./layout/navbar" as *;
@use "./abstracts/mixins" as *;
@use "./pages/services" as *;
@use "./pages/about" as *;
@use "./pages/process" as *;
@use "./pages/faq" as *;
@use "./pages/quote" as *;
@use "./pages/contact" as *;
@use "./layout/footer" as *;

.section {
	&-padding {
		padding: 10em 0;
	}
	&__box {
		&-heading {
			color: $main-color;
			text-transform: uppercase;
			font-weight: 500;
			font-size: clamp(1.6rem, 1.2rem + 1.1111vw, 2.2rem);
		}
		&-text {
			margin: 0.2em 0;
			color: #fff;
			font-size: clamp(2.4rem, 1.3333rem + 2.963vw, 4rem);
			font-weight: 600;
		}
		&-description {
			color: #c7c7c7;
			font-size: clamp(1.6rem, 1.4667rem + 0.3704vw, 1.8rem);
		}
	}
}
@media (max-width: 991px) {
	.navbar__items {
		display: flex;
		position: absolute;
		flex-direction: column;
		justify-content: center;
		gap: 6em;
		height: calc(100dvh - 60px);
		width: 100%;
		background-color: #000;
		left: 0;
		right: 0;
		top: 60px;
		bottom: 0;
		transition: transform 0.6s;
		transform: translateX(100%);
	}
}

@media (min-width: 576px) {
	.header {
		&__buttons {
			flex-direction: row;
			&-button {
				&--first {
				}
			}
		}
	}
	.swiper {
		&__image {
			display: block;
		}
	}
	.container{
		padding: 0 8em;
	}

}

// Medium devices (tablets, 768px and up)
@media (min-width: 768px) {
	.header {
		&__buttons {
			gap: 6em;
			&-button {
				max-width: 320px;
				width: max-content;
				&--first {
					width: 100%;
				}
			}
		}
	}
	.services__box,
	.our-experts,
	.guarantee__box {
		grid-template-columns: repeat(2, 1fr);
	}
	.individual-approach {
		&__box {
			flex-direction: row;
			&-text,
			&-image {
				flex: 1;
			}
			&-text {
				gap: 8em;
			}
		}
	}
}

// Large devices (desktops, 992px and up)
@media (min-width: 992px) {
	.section{
		&__box{
			&-description{
				max-width: 65%;
			}
		}
	}
	.accordion {
		display: flex;
		flex-direction: row;
		gap: 4em;
	}
	.navbar {
		&__content {
			height: 80px;
		}
		&__links {
			display: flex;
		}
		&__socialmedia {
			&-mobile {
				display: none;
			}
			&-desktop {
				display: flex;
				flex-direction: row;
			}
		}
		&__items {
			display: flex;
			gap: 4em;
		}
		.navbar__link {
			font-size: 2rem;
		}
		.burger {
			display: none;
		}
	}
	.services__box,
	.our-experts,
	.guarantee__box {
		grid-template-columns: repeat(3, 1fr);
	}
	.our-experts {
		&__expert {
			// padding: 40em 10em;
		}
	}
	.contact {
		&__box {
			&-item{
				gap: 4em;
			}
			&-icon {
				padding: 4em;
				& img {
					height: 40px;
					width: 40px;
				}
			}
		}
	}
}

// X-Large devices (large desktops, 1200px and up)
@media (min-width: 1200px) {
	.section-padding {
		padding: 15em 0;
	}
	.header {
		height: 75dvh;
	}
}

// XX-Large devices (larger desktops, 1400px and up)
@media (min-width: 1400px) {
}
