@use "../base/colors" as *;
@use "../abstracts/mixins" as *;

.contact {

	&__box {
        display: flex;
        gap: 6em;
        flex-wrap: wrap;
        justify-content: space-between;
        margin-top: 10em;
		&-icon {
			background-color: #141414;
			padding: 3em;
            border-radius: 50%;
			& img {
				height: 30px;
				width: 30px;
			}
		}
		&-item {
            display: flex;
            gap: 2em;
            align-items: center;
			& p {
				color: #fff;
				font-size: clamp(1.6rem, 1.3333rem + 0.7407vw, 2rem);
			}
		}
	}
}
