@use "./abstracts/mixins" as *;
@use "../base/colors" as *;
.about-us {
	&__box:nth-child(3) {
		background-color: hsl(0, 0%, 4%);
	}
	&-line {
		height: 0.3rem;
		background-color: #d2d8f9;
		max-width: 200px;
		width: 15%;
	}
	&__box {
		padding: 6em 0;
		&-description {
			color: #c7c7c7;
			font-size: clamp(1.8rem, 1.4rem + 1.1111vw, 2.4rem);
		}
		& span {
			display: flex;
			align-items: center;
			margin: 2em 0;
		}
		&-heading {
			font-size: clamp(2rem, 1.2rem + 2.2222vw, 3.2rem);
			font-weight: 500;
			color: #d2d8f9;
			margin: 0 0.5em;
		}
	}
}
.our-experts {
	display: grid;
	grid-template-columns: repeat(1, 1fr);
	gap: 4em;
	margin-top: 10em;
	&__expert {
		display: flex;
		align-items: flex-end;
		position: relative;
		background-size: cover;
		min-height: 300px;
		height: calc(300px + 15vh);
		padding: 4em 6em;
		background-position: center;
		z-index: 1;
		& p {
			color: #fff;
			font-size: clamp(1.6rem, 1.2rem + 1.1111vw, 2.2rem);
			font-weight: 400;
			opacity: 1;
			transition: opacity 0.6s;
		}
		&:hover {
			&::after {
				background: none;
			}
			& p {
				opacity: 0;
				pointer-events: none;
			}
		}
		&::after {
			background: linear-gradient(
				to top,
				rgba(10, 10, 10, 1),
				rgba(10, 10, 10, 0)
			);
			position: absolute;
			bottom: 0;
			left: 0;
			width: 100%;
			height: 100%; /* Wysokość gradientu */
			pointer-events: none;
			content: "";
			z-index: -1;
		}
		z-index: 1;
		&:nth-child(1) {
			background-image: url("../../../dist/img/first_woman.jpg");
		}
		&:nth-child(2) {
			background-image: url("../../../dist/img/man.jpg");
		}
		&:nth-child(3) {
			background-image: url("../../../dist/img/woman_second.jpg");
		}
	}
}
.individual-approach {
	&__box {
		display: flex;
		flex-direction: column-reverse;
		justify-content: space-evenly;
		margin-top: 5em;
		gap: 3em;
		&-text {
			display: flex;
			flex-direction: column;
			justify-content: center;
			gap: 4em;
		}
		&-image {
			& img {
				height: 100%;
				width: 100%;
			}
		}
	}
	&__button {
		display: flex;
		align-items: center;
		gap: 1em;
		justify-content: center;
		font-size: clamp(1.6rem, 1.3333rem + 0.7407vw, 2rem);
		color: #fff;
		background-color: #141414;
		padding: 0.6em 1.2em;
		width: max-content;
	}
}
.guarantee {
	.about-us__box-description {
		// max-width: 65%;
		margin-top: 2em;
	}
	&__box {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		margin-top: 15em;
		gap: 4em;
		&-item {
			display: flex;
			flex-direction: column;
			justify-content: center;
			align-items: center;
			gap: 2em;
			padding: 6em 8em;
			border: 2px solid hsl(0, 0%, 11%);
			background-color: #141414;
			border-radius: 4px;
			text-align: center;
			& p {
				font-size: clamp(4rem, 2.4rem + 4.4444vw, 6.4rem);
				color: #9E9E9E;
				line-height: 1;
				font-weight: 600;
			}
			& h4 {
				color: #fff;
				font-size: clamp(1.8rem, 1.4rem + 1.1111vw, 2.4rem);
				font-weight: 400;
			}
		}
	}
}
