@use "./abstracts/mixins" as *;
@use "./base/colors" as *;

.services {
	&__box {
		display: grid;
		grid-template-columns: repeat(1, 1fr);
		margin-top: 12em;
		gap: 4em;
		&-item {
            display: flex;
            align-items: flex-end;
            padding: 2em 4em;
            position: relative;
			width: 100%;
			height: auto;
            min-height: 220px;
			background-size: cover;
			background-position: center;
			background-color: #c0c0c0;
			border-bottom: 2px solid $background-color;
			z-index: 1;
            & p{
                color: #fff;
                font-size: clamp(1.6rem, 1.2rem + 1.1111vw, 2.2rem);
                font-weight: 400;
            }
			&:nth-child(1) {
				background-image: url("../../../dist/img/tahamie-farooqui-IU-mLTtrJgo-unsplash.jpg");
			}
			&:nth-child(2) {
				background-image: url("../../../dist/img/un-liu-yqsgL2wKEHA-unsplash.jpg");
			}
			&:nth-child(3) {
				background-image: url("../../../dist/img/car_vent.jpg");
			}
			&::after {
				position: absolute;
				content: "";
				top: 0;
				left: 0;
                bottom: 0;
				width: 100%;
				height: 100%;
				background-color: rgba(0, 0, 0, 0.5);
				z-index: -1;
			}
			transition: border-bottom 0.3s;
			&:hover {
				border-bottom: 2px solid #fff;
			}
		}
	}
}
