@use "../base/colors" as *;
@use "../abstracts/mixins" as *;

.faq {
	&__question {
		margin-top: 10em;
        &-text{
            font-size: clamp(1.6rem, 1.3333rem + 0.7407vw, 2rem);
            color: #cbcbcb;
        }
		&-button {
			display: flex;
			align-items: center;
			gap: 1em;
			justify-content: center;
			font-size: clamp(1.6rem, 1.3333rem + 0.7407vw, 2rem);
			color: #fff;
			background-color: #141414;
            margin: 1em 0;
			padding: 0.6em 1.2em;
			width: max-content;
		}
	}
}
.accordion {
	margin: 10em auto;
	width: 100%;
	&__underline {
		width: 100%;
		height: 3px;
		background-color: #333333;
		border-radius: 120px;
	}
	&__column {
		display: flex;
		flex-direction: column;
	}
	&__box {
		margin: 2em 0;
		.plus {
			font-size: 4rem;
			font-weight: 300;
		}
		&-btn {
			font-weight: 400;
			display: flex;
			justify-content: space-between;
			align-items: center;
			padding: 0.6em 2em;
			width: 100%;
			border: none;
			background-color: #141414;
			color: #fff;
			font-size: clamp(1.6rem, 1.3333rem + 0.7407vw, 2rem);
			text-align: left;
			transition: background-color 0.3s;
			cursor: pointer;
			&:hover {
				background-color: #191919;
			}
		}
	}
	&__info {
		color: #c7c7c7;
		font-size: clamp(1.6rem, 1.375rem + 0.625vw, 2rem);
		transition: max-height 0.6s ease;
		pointer-events: none;
		max-height: 0;
		overflow: hidden;
		&.active {
			max-height: 350px;
		}
		&-text {
			padding: 1em 1em;
			font-size: clamp(1.6rem, 1.4875rem + 0.3125vw, 1.8rem);
		}
	}
}
