@use "../abstracts/mixins" as *;
@use "../base/colors" as *;
.header {
	display: flex;
	justify-content: center;
	align-items: center;
	background-image: url("../../../dist/img/header_large.jpg");
	background-position: center;
	background-size: cover;
	width: 100%;
	height: 60dvh;
	min-height: 600px;
	position: relative;
	z-index: 1;
	&::after {
		position: absolute;
		content: "";
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.7);
		z-index: -1;
	}
	&::before {
		background: linear-gradient(
			to top,
			rgba(10, 10, 10, 1),
			rgba(10, 10, 10, 0)
		);
		position: absolute;
		bottom: 0;
		left: 0;
		width: 100%;
		height: 80%; /* Wysokość gradientu */
		pointer-events: none;
		content: "";
		z-index: -2;
	}
	&__box {
		display: flex;
		flex-direction: column;
		justify-content: center;
		gap: 0.3em;text-align: center;
		&-heading {
			font-size: clamp(3rem, 0.6667rem + 6.4815vw, 6.5rem);
			color: $main-color;
			font-weight: 500;
            margin: 0.3em 0;
		}
		&-text {
			color: #fff;
			font-size: clamp(1.8rem, 0.8667rem + 2.5926vw, 3.2rem);
		}
	}
	&__buttons {
		display: flex;
		justify-content: center;
		flex-direction: column;
		width: 100%;
		gap: 2em;
		margin-top: 4em;
		&-button {
			width: 100%;
			font-size: clamp(1.6rem, 1.3333rem + 0.7407vw, 2rem);
			padding: 0.6em 1.2em;
			border-radius: 6px;
			text-align: center;
			&--first {
				background-color: $main-color;
				color: #000;
				transition: background-color 0.3s;
				&:hover {
					background-color: $hover-color;
				}
			}
			&--second {
				color: #fff;
				border: 2px solid #fff;
			}
		}
	}
}
