@use "../base/colors" as *;
@use "../abstracts/mixins" as *;

.swiper {
	width: 100%;
	height: 300px;
	margin-top: 10em;
	&__image {
		display: none;
		width: 50%;
		height: 100%;
		object-fit: cover;
	}
	&__text {
		display: flex;
		flex-direction: column;
		padding: 8em 6em;
		justify-content: space-between;
		&-heading {
			color: #fff;
			font-size: clamp(1.8rem, 1.5333rem + 0.7407vw, 2.2rem);
		}
		&-text {
			color: #c7c7c7;
			font-size: clamp(1.6rem, 1.4667rem + 0.3704vw, 1.8rem);
		}
	}
	&__stars {
		display: flex;
		gap: 2em;
		&-star {
			height: 30px;
			width: auto;
		}
	}
}

.swiper-slide {
	display: flex;
	gap: 2em;
	overflow: hidden;
	background-color: hsl(0, 0%, 6%);
	cursor: grab;
}

.quote {
	position: relative;
	background-image: url("../../../dist/img/quote2.jpg");
	background-size: cover;
	background-position: 50% 75%;
    background-attachment: fixed;
    text-align: center;
	z-index: 1;
	&::after {
        position: absolute;
		content: "";
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(0, 0, 0, 0.8);
		z-index: -1;
	}
	&__text {
        display: flex;
        padding: 6em 0;
        gap: 1em;
		align-items: center;
		flex-direction: column;
		&-heading {
			font-size: clamp(2.2rem, 1rem + 3.3333vw, 4rem);
			color: #fff;
			font-weight: 500;
		}
		&-text {
			color: #c7c7c7;
			font-size: clamp(1.6rem, 1.4667rem + 0.3704vw, 1.8rem);
		}
	}
}
